import moment from 'moment-es6';
import {FormControl} from '@angular/forms';

export class Calendar2 {
  initialDate?: moment.Moment;
  activeDate?: moment.Moment;
  filter?: (date: moment.Moment, formControl?: FormControl) => boolean;
  range?: [moment.Moment | null, moment.Moment | null];
  disabled?: boolean;
  readonly?: boolean;
  hideControls?: boolean;
  expandedControlsStyle?: boolean;
}

export class Calendar2Day {
  date: moment.Moment;
  isSelected: boolean;
  isToday: boolean;
  isOtherMonth: boolean;
  isDisabled: boolean;
  isInRange: boolean;
  isRangeFrom: boolean;
  isRangeTo: boolean;
  isReadonly: boolean;
}

